(function() {
  this.App.controller('StaffController', ["$log", "$stateParams", "$window", "$uibModal", "toastr", "MnoConfirm", "MnoeAdminConfig", "MnoeCurrentUser", "MnoeUsers", "MnoeSubTenants", "ADMIN_ROLES", function($log, $stateParams, $window, $uibModal, toastr, MnoConfirm, MnoeAdminConfig, MnoeCurrentUser, MnoeUsers, MnoeSubTenants, ADMIN_ROLES) {
    'ngInject';
    var vm;
    vm = this;
    vm.isSaving = false;
    vm.adminRoles = ADMIN_ROLES;
    vm.isAdmin = MnoeCurrentUser.user.admin_role === 'admin';
    vm.isSubTenantEnabled = MnoeAdminConfig.isSubTenantEnabled();
    MnoeUsers.get($stateParams.staffId).then(function(response) {
      vm.staff = response.data;
      vm.staff.admin_role_was = vm.staff.admin_role;
      vm.staff.subTenantName = function() {
        return _.find(vm.subTenants, function(subTenant) {
          return subTenant.id === vm.staff.mnoe_sub_tenant_id;
        }).name;
      };
      vm.staff.adminRoleName = function() {
        return _.find(vm.adminRoles, function(role) {
          return role.value === vm.staff.admin_role;
        }).label;
      };
      if (vm.staff.mnoe_sub_tenant_id) {
        return MnoeSubTenants.get(vm.staff.mnoe_sub_tenant_id).then(function(r) {
          return vm.staff.subTenants = r.data.sub_tenants;
        });
      }
    });
    MnoeSubTenants.list(null, null, null).then(function(response) {
      return vm.subTenants = response.data;
    });
    vm.updateStaff = function() {
      var modalOptions, updateStaffAction;
      vm.isSaving = true;
      updateStaffAction = function() {
        return MnoeUsers.updateStaff(vm.staff).then(function(response) {
          vm.staff = response.data.user;
          vm.staff.admin_role_was = vm.staff.admin_role;
          return toastr.success("mnoe_admin_panel.dashboard.staff.update_staff.toastr_success", {
            extraData: {
              staff_name: vm.staff.name + " " + vm.staff.surname
            }
          });
        }, function(error) {
          toastr.error("mnoe_admin_panel.dashboard.staff.update_staff.toastr_error", {
            extraData: {
              staff_name: vm.staff.name + " " + vm.staff.surname
            }
          });
          return $log.error("An error occurred while updating staff:", error);
        })["finally"](function() {
          return vm.isSaving = false;
        });
      };
      if (vm.staff.admin_role_was === 'staff' && vm.staff.admin_role !== 'staff' && vm.staff.client_ids.length) {
        modalOptions = {
          closeButtonText: 'mnoe_admin_panel.dashboard.staff.update_staff_role.cancel',
          actionButtonText: 'mnoe_admin_panel.dashboard.staff.update_staff_role.action',
          headerText: 'mnoe_admin_panel.dashboard.staff.update_staff_role.proceed',
          bodyText: 'mnoe_admin_panel.dashboard.staff.update_staff_role.perform'
        };
        return MnoConfirm.showModal(modalOptions).then(updateStaffAction)["finally"](function() {
          return vm.isSaving = false;
        });
      } else {
        return updateStaffAction();
      }
    };
    vm.updateClientsModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/staff/update-staff-clients-modal/update-staff-clients.html',
        controller: 'UpdateStaffClientsController',
        controllerAs: 'vm',
        resolve: {
          staff: function() {
            return vm.staff;
          }
        }
      });
      return modalInstance.result.then(function(staff) {
        return vm.staff = staff;
      });
    };
  }]);

}).call(this);
