(function() {
  this.App.directive('mnoeStaffsLocalList', ["ADMIN_ROLES", function(ADMIN_ROLES) {
    return {
      restrict: 'E',
      scope: {
        list: '='
      },
      templateUrl: 'app/components/mnoe-staffs-local-list/mnoe-staffs-local-list.html',
      link: function(scope, _elem, _attrs) {
        return scope.getAdminRoleLabel = function(staff) {
          return _.find(ADMIN_ROLES, function(role) {
            return role.value === staff.admin_role;
          }).label;
        };
      }
    };
  }]);

}).call(this);
